@import url(https://fonts.googleapis.com/css2?family=Merriweather&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Merriweather', serif;
}

.App {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  min-height: 100vh;
  padding: 30px;
  grid-gap: 5%;
  gap: 5%;
}
.App .side-bar {
  -webkit-order: 2;
          order: 2;
  top: 3rem;
  left: 3rem;
}
.App .input-area {
  -webkit-order: 1;
          order: 1;
  top: 3rem;
  right: 3rem;
}

@media(min-width: 801px){
  .App {
    min-height: 100vh;
    padding: 30px;
    grid-gap: 5%;
    gap: 5%;
  }
  .App .side-bar {
    position: absolute;
    top: 3rem;
    left: 3rem;
  }
  .App .input-area {
    position: fixed;
    top: 3rem;
    right: 3rem;
  }
}

.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.2);

  display: -webkit-flex;

  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.popup-inner {
  position: relative;
  padding: 32px;
  width: 100%;
  max-width: 640px;
  background-color: rgb(255, 255, 255);
}
.popup-inner .close-btn {
  position: absolute;
  top: 16px;
  right: 16px;
}

.item-popup {
  padding: 1rem;
}
.item-popup h3 {
  margin: 10px 0px;
  letter-spacing: 1px;
}
.item-popup p {
  letter-spacing: 1px;
}

.close-btn {
  background-color: black;
  color: white;
  padding: 0.5em 2em;
}
.input-text textarea {
  padding: 1rem;
  width: 100%;
}
.input-title input {
  margin: 1rem 0;
  padding: 0.7rem;
  width: 60%;
}
.input-title {
  margin: 10px 0px;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.add-btn {
  background-color: black;
  color: white;
  padding: 0.2em 1em;
}

@media(min-width: 768px){
  .input-title {
    margin: 10px 0px;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
  }
  .input-title input {
    margin: 1rem 0;
    padding: 0.7rem;
    width: 80%;
  }
  .add-btn {
    background-color: black;
    color: white;
    padding: 0.2em 3em;
  }
  
  .input-text {
    min-height: 100vh;
    width: auto;
  }
  .input-text textarea {
    padding: 1rem;
  }
}
.item {
  width: 45vh;
  margin: 10px 0px;
  background-color:hsl(0, 10%, 90%);
  padding: 20px;
}
.item p {
  font-size: 0.7rem;
}
.item .delete-btn {
  height: 40px;;
  width: 40px;
  color: hsl(0, 54%, 59%);
  background: transparent;
}
.item .preview-btn {
  background-color:hsl(0, 0%, 0%);
  padding: 5px 10px;
  color: white;
  margin-top: 10px;
}
.title {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

@media(max-width: 768px){
  .item {
    width: 100%;
  }
}
